/* eslint-disable no-restricted-globals */
import React, {useState, useContext, useEffect} from 'react'
import { SelectProfileContainer } from './profiles';
import { FooterContainer } from '../containers/footer';
import {FirebaseContext } from '../context/firebase';
import { Header, Loading, Card,Player} from '../components';
import requests from '../utils/requests';
import axios from '../utils/axios';
import * as ROUTES from '../constants/routes';
import logo from '../logo.svg';
import { useHistory } from 'react-router-dom';
import * as SOURCE from '../constants/source';
import {  FaInfoCircle, FaPlay } from 'react-icons/fa';

import Fuse from "fuse.js";

 

const baseURL = 'https://image.tmdb.org/t/p/original/';

//    title mediaType .
export function BrowseContainer({ slides ,id }) {
  const { firebase } = useContext(FirebaseContext);
  const user = firebase.auth().currentUser || {};
  
  //   
  const [category,setCategory] = useState('series');
  const [searchTerm, setSearchTerm] = useState('');
  const [slideRows, setSlideRows] = useState([]);

  //  ==    
  const [profile, setProfile] = useState([]);
  const [loading,setLoading] = useState(true);
 
  
  const [background, setBackGround] = useState([])

  
  // 
  const history = useHistory();
  const [value] = React.useState(0);
 
 
  
  useEffect(() => {
    if (value === 0) history.push('/browse');
    if (value === 1) history.push('tv');
    if (value === 2) history.push('movies');

  }, [value,history])

  const [handleShow] = useState(false);
 
 
  


  useEffect(() => {


    const navHandler = () => {
      if(window.scrollY > 50) {
        handleShow(true);
      } else {
        handleShow(false);
      }
    };

      window.addEventListener("scroll", navHandler);

      return () => {
        //clean up
        window.removeEventListener("scroll", navHandler);
      }

   },[]);
 
    useEffect(() => {
        setTimeout(() => {
            setLoading(false)
        }, 2000)
    }, [profile.displayName]);

 
  
     useEffect(() => {
         setSlideRows(slides[category]);
     }, [slides, category]);


  //   clen up 
     useEffect(() => {
       return () => setLoading(false);
     }, [loading]);

     useEffect(() => {
      async function fetchData() {
          const request = await axios.get(requests.fetchNetflixOriginals);
          setBackGround(
              request.data.results[
              Math.floor(Math.random() * request.data.results.length - 1)
              ]
          );
          return request;
      }
      fetchData();
  }, []);
 

  
   
     useEffect(() => {
         const fuse = new Fuse(slideRows,
          { keys: ['data.description','data.title', 'data.genre'] });
        
        
          const results = fuse.search(searchTerm).map(({ item }) => item);

        if(slideRows.length > 0 && searchTerm.length > 3 && results.length > 0) {
            setSlideRows(results);
        } else {
            setSlideRows(slides[category]);
        }
 
    }, [searchTerm]);

   
     
    return profile.displayName ? (
      <>

  
      {loading ? <Loading src={user.photoURL} /> : <Loading.ReleaseBody /> }
 
        
          <Header.SubBackground src={background.backdrop_path !== undefined ? `${SOURCE.BASE_IMG_URL}${background.backdrop_path}` : '/images/misc/home-bg.jpg'}>
            
                <Header.Banner src={`${baseURL}${background?.backdrop_path}`}>
               
                <Header.Frame>
                {/*  Nav  */}
                {/*  selection -filter    */}
                    <Header.Group>
                      <Header.Logo to={ROUTES.HOME} src={logo} alt="Netflix"/>
                      <Header.TextLink active={category === 'trending' ? 'true' : 'false'} 
                        onClick={() => setCategory('trending')}
                      >
                        
                      </Header.TextLink>
                      <Header.TextLink 
                          active={category === 'tv' ? 'true' : 'false'} 
                          onClick={() => setCategory('series')}>
                        tv
                      </Header.TextLink>
                      <Header.TextLink 
                          active={category === 'movie' ? 'true' : 'false'} 
                          onClick={() => setCategory('films')}>
                        
                      </Header.TextLink>
                     
                        <Header.TextLink>
              
                                  
                        </Header.TextLink>
  
                    </Header.Group>
                    

                    {/*  Nav  */}
                     {/*   */}
                    <Header.Group>
                      <Header.SearchComponent>
                            <Header.Search searchTerm={searchTerm} setSearchTerm={setSearchTerm}/>
                        </Header.SearchComponent>
                               <Header.TextLink 
                          active={category === 'tv' ? 'true' : 'false'} 
                          onClick={() => setCategory('series')}>
                        
                      </Header.TextLink>
                        <Header.Profile>
                            <Header.Picture src={user.photoURL} />
                            <Header.Dropdown>
                                <Header.Group>
                                    <Header.Picture src={user.photoURL} />
                                    <Header.TextLink>{user.dispalyName}</Header.TextLink>
                                </Header.Group>
                                <Header.ButtonGroup>
                                    <Header.TextLinks onClick={() => firebase.auth().signOut()}> </Header.TextLinks>
                                    <Header.TextLinks onClick={() => firebase.auth().signOut()}></Header.TextLinks>
                                    <Header.TextLinks onClick={() => firebase.auth().signOut()}></Header.TextLinks>
                                    <Header.TextLinks onClick={() => firebase.auth().signOut()}></Header.TextLinks>
                                    <Header.TextLinks onClick={() => firebase.auth().signOut()}> </Header.TextLinks>
                              </Header.ButtonGroup>
                                
                            </Header.Dropdown>
                        </Header.Profile>
                    </Header.Group>
                </Header.Frame>
                <Header.Feature>
                   <Header.FeatureCallOut>{background?.title || background?.name}</Header.FeatureCallOut>
                   <Header.Text>{background.original_name}</Header.Text>
                   <Header.Text>{background.overview}</Header.Text>
                   <Header.PlayButton>
                   <Header.InfoH1>
                         
                    </Header.InfoH1>
                    <FaPlay />
                   </Header.PlayButton>
                   <Header.InfoButton>
                    <Header.InfoH1>
                         More Info
                    </Header.InfoH1>
                    <FaInfoCircle />
                   </Header.InfoButton>
                </Header.Feature>
              </Header.Banner>
          </Header.SubBackground>       
 

          <Card.Group>

             {slideRows.map((slideItem) => (
               <Card key={`${category}-${slideItem.title.toLowerCase()}`}>
            <Card.Title>{slideItem.title}</Card.Title>
            <Card.Left>

            </Card.Left>
            <Card.Slider>
            <Card.Entities id={id}>
              {slideItem.data.map((item) => (
               <Card.Item key={item.docId || item.id} item={item}>
                  <Card.Image src={`${SOURCE.BASE_IMG_URL}${item.poster_path}` || `images/${category}/${item.genre}/${item.slug}/smail.jpg`} />
                </Card.Item>
              ))}
            </Card.Entities>
            </Card.Slider>
            <Card.Right>

            </Card.Right>
            

            <Card.Feature category={category}>
              <Player>
                <Player.Button />
                <Player.Video />
              </Player>
            </Card.Feature>
            
          </Card>
        ))}
   
      </Card.Group>
         
        <FooterContainer />      
        </>
    ) : (
      <SelectProfileContainer user={user} setProfile={setProfile} />
    
    )
}